import React, {useState} from 'react';
import {Collapse, Nav, Navbar, NavbarBrand, NavbarText, NavbarToggler, NavItem, NavLink} from "reactstrap";
import Footer from "./Footer";
import {Link} from "react-router-dom";

const Contact = () => {
    const toggle = () => setIsOpen(!isOpen);
    const [isOpen, setIsOpen] = useState(false);
    return (
        <>
            <section className="about contact">
                <section className="menu bg-white">
                    <div className="container">
                        <Navbar light expand="md">
                            <Link to="/"><NavbarBrand><img src="https://www.maxway.uz/logo.svg" alt=""/></NavbarBrand></Link>
                            <NavbarToggler onClick={toggle}/>
                            <Collapse isOpen={isOpen} navbar>
                                <Nav className="mr-auto" navbar>
                                    <NavItem>
                                        <Link to="/"><NavLink>Главная</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/branches"><NavLink>Филиалы</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/about"><NavLink>О нас</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/contact"> <NavLink>Контакты</NavLink></Link>
                                    </NavItem>
                                </Nav>
                                <NavbarText className="d-flex">
                                    <div className="d-flex align-items-center korzina">
                                        <img src="https://www.maxway.uz/images/Icons/shopping-cart.svg" alt=""/>
                                        {/*<div className="redd"><span></span></div>*/}
                                        <h6 className="mb-0">Корзина</h6>

                                    </div>
                                    <div>
                                        <button className="btn ">Войти</button>
                                    </div>
                                </NavbarText>
                            </Collapse>
                        </Navbar>
                    </div>
                </section>
                <div className="container mt-5 mb-5">
                    <div className="row mt-5 mb-5">
                        <div style={{marginBottom:'200px', marginTop:"50px"}} className="col-7">
                            <div className="card mb-5">
                                <div className="card-body ">
                                    <h1>Контакты</h1>
                                    <div className="d-flex justify-content-between align-items-center mt-5 pb-3 ">
                                        <h6 className="mt-0 mb-0">Единный call-центр</h6>
                                        <a className="mb-0" href="tel:+998712005400">+998 71 200 54 00</a>
                                    </div>
                                    <p>Вы можете написать нам <a href="https://t.me/maxwaymasterfoodsupport">@MaxWaySupport_bot</a> также вы можете звонить по номеру
                                        +998712005400.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer/>
            </section>
        </>
    );
};

export default Contact;