import React, {useState} from 'react';
import {Collapse, Nav, Navbar, NavbarBrand, NavbarText, NavbarToggler, NavItem, NavLink} from "reactstrap";
import Footer from "./Footer";
import {Link} from "react-router-dom";

const About = () => {
    const toggle = () => setIsOpen(!isOpen);
    const [isOpen, setIsOpen] = useState(false);
    return (
        <>
            <section className="about">
                <section className="menu bg-white">
                    <div className="container">
                        <Navbar light expand="md">
                            <Link to="/"><NavbarBrand><img src="https://www.maxway.uz/logo.svg" alt=""/></NavbarBrand></Link>
                            <NavbarToggler onClick={toggle}/>
                            <Collapse isOpen={isOpen} navbar>
                                <Nav className="mr-auto" navbar>
                                    <NavItem>
                                        <Link to="/"><NavLink>Главная</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/branches"><NavLink>Филиалы</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/about"><NavLink>О нас</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/contact"> <NavLink>Контакты</NavLink></Link>
                                    </NavItem>
                                </Nav>
                                <NavbarText className="d-flex">
                                    <div className="d-flex align-items-center korzina">
                                        <img src="https://www.maxway.uz/images/Icons/shopping-cart.svg" alt=""/>
                                        {/*<div className="redd"><span></span></div>*/}
                                        <h6 className="mb-0">Корзина</h6>

                                    </div>
                                    <div>
                                        <button className="btn ">Войти</button>
                                    </div>
                                </NavbarText>
                            </Collapse>
                        </Navbar>
                    </div>
                </section>
                <div className="container mt-5 mb-5">
                    <div className="row">
                        <div className="col-12 mt-5 mb-5">
                            <div className="card">
                                <div className="card-body">
                                    <img className="w-100" src="https://www.maxway.uz/images/Rectangle/about.png" alt=""/>
                                    <h6>О компании</h6>
                                    <p>Компания была основана в феврале 2005 года в Ташкенте. На сегодняшний момент у
                                        компании 11 филиалов в Ташкенте.
                                        Меню состоит в основном из клаб сендвичей, хот-догов, бургеров, лавашей и
                                        донаров. Наши приоритеты – свежесть и качество ингредиентов, разнообразие
                                        начинок, доступные цены и внимание к просьбам гостей.
                                        Ежедневно в MaxWay заказывают большое количество самых разных людей. И мы
                                        стараемся увеличивать как число посетителей, так и число филиалов. С каждым
                                        приготовленным блюдом мы оттачиваем детали фирменных рецептов и ищем идеальный
                                        баланс цены и качества, чтобы и дальше оставаться вашим любимым брендом.
                                        Если вдруг вы столкнулись с плохим обслуживанием или низким качеством
                                        приготовленной еды с нашей стороны, обязательно напишите нам на
                                        <a href="https://t.me/maxwaymasterfoodsupport">https://t.me/maxwaymasterfoodsupport</a>. Мы с радостью принимаем как положительные,
                                        так и отрицательные отзывы. Жалоба гостя – подарок, благодаря которому нам есть
                                        куда расти.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer/>
            </section>
        </>
    );
};

export default About;