import React, {useState} from 'react';
import {Collapse, Nav, Navbar, NavbarBrand, NavbarText, NavbarToggler, NavItem, NavLink} from "reactstrap";
import accordion from "./accordion";
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Footer from "./Footer";
import {Link} from "react-router-dom";
// const useStyles = makeStyles((theme) => ({
//     root: {
//         width: '100%',
//     },
//     heading: {
//         fontSize: theme.typography.pxToRem(15),
//         fontWeight: theme.typography.fontWeightRegular,
//     },
// }));
const Branches = () => {
    // const classes = useStyles();

    const toggle = () => setIsOpen(!isOpen);
    const [isOpen, setIsOpen] = useState(false);
    const [style,setStyle]=useState('none')
    const [styleOne,setStyleOne]=useState("block")
    const [branchs,setBranchs]=useState(accordion)
    return (
        <>
            <section className="branches">
                <section className="menu bg-white">
                    <div className="container">
                        <Navbar  light expand="md">
                            <Link to="/"><NavbarBrand><img src="https://www.maxway.uz/logo.svg" alt=""/></NavbarBrand></Link>
                            <NavbarToggler onClick={toggle}/>
                            <Collapse isOpen={isOpen} navbar>
                                <Nav className="mr-auto" navbar>
                                    <NavItem>
                                        <Link to="/"><NavLink>Главная</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/branches"><NavLink>Филиалы</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/about"><NavLink>О нас</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/contact"> <NavLink>Контакты</NavLink></Link>
                                    </NavItem>
                                </Nav>
                                <NavbarText className="d-flex">
                                    <div className="d-flex align-items-center korzina">
                                        <img src="https://www.maxway.uz/images/Icons/shopping-cart.svg" alt=""/>
                                        {/*<div className="redd"><span></span></div>*/}
                                        <h6 className="mb-0">Корзина</h6>

                                    </div>
                                    <div>
                                        <button className="btn ">Войти</button>
                                    </div>
                                </NavbarText>
                            </Collapse>
                        </Navbar>
                    </div>
                </section>

                <div className="container pt-5">
                    {
                        branchs.map((item,index)=>{
                            const{id,littleTitle,title,orienter}=item;
                            return(
                                <div >
                                    <Accordion>
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel1a-content"
                                            id="panel1a-header"
                                        >
                                            <div className="d-flex flex-column">
                                                <Typography className="title" >{title}</Typography>
                                                <Typography className="little-title">{littleTitle}</Typography>
                                            </div>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            <Typography>
                                               <div className="d-flex align-items-center">
                                                   <div className="ww-50">
                                                       <h6 className="title">{title}</h6>
                                                       <div className="d-flex justify-content-start align-items-center">
                                                           <i className="fas fa-map-marker-alt"/>
                                                           <p className="little-title mt-3">Адрес: {littleTitle}</p>
                                                       </div>
                                                       <div className="d-flex justify-content-start align-items-center">
                                                           <i className="far fa-clock"/>
                                                           <p className="little-title mt-3">Часы работы: 10:00-03:00</p>
                                                       </div>
                                                       <div className="d-flex justify-content-start align-items-center">
                                                           <i className="fas fa-phone"/>
                                                           <p className="little-title mt-3">Номер телефона: +998 71 200 54 00</p>
                                                       </div>
                                                       <div className="d-flex justify-content-start align-items-center">
                                                           <i className="fas fa-location-arrow"/>
                                                           <p className="little-title mt-3">Ориентир: {orienter}</p>
                                                       </div>
                                                   </div>
                                                   <div className="ww-50">
                                                       <img className='ww-75' src="https://www.maxway.uz/images/Rectangle/max-way.png" alt=""/>
                                                   </div>
                                               </div>
                                            </Typography>
                                        </AccordionDetails>
                                    </Accordion>
                                </div>
                            )
                        })
                    }
                </div>
                <Footer/>
            </section>

        </>
    );
};

export default Branches;